*{  
    
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
header{
    width: 100%;
    height: 65px;
    background-color:#111011;
    color: #fefefe;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 100px;
}
.logo{
     
    font-size: 22px;
    font-weight: bold;
    
}
.hamburger{
    display: none;
}
.nav-bar ul{
    display: flex;
}
.nav-bar ul li a{
    display:block;
    color: inherit;
    font-size: 18px;
    padding: 5px 10px;
    margin: 0 5px;
    border-radius: 15px;
}
.nav-bar ul li a:hover{
     
   color: black;
   background-color:#fefefe;
   transition:0.2s;
     
}
@media only screen and (max-width:802px){
    header{
         
        padding: 0 50px;
    }
}
@media only screen and (max-width:703px){
    header{
         
        padding: 0 20px;
    }
}
@media only screen and (max-width:642px){
     .hamburger{
        display: block;
     }
     .hamburger .line{
         width: 30px;
         height: 3px;
         background-color: #fefefe;
         margin: 6px 0;
     }
     .nav-bar{
        position: absolute;
         
        top: 65px;
        left: 0px;
        right: 0px;
        width: 100vw;
        height:200px;
        background-color: #11101b;
        display: block;
     }
     .nav-bar ul  {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
     }

}


.toggle-switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 20px;
    margin-left: 10px;
  }
  
  .switch input[type="checkbox"] {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: 0.4s;
    border-radius: 34px;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 16px;
    width: 16px;
    left: 2px;
    bottom: 2px;
    background-color: white;
    transition: 0.4s;
    border-radius: 50%;
  }
  
  input[type="checkbox"]:checked + .slider {
    background-color: #2196f3;
  }
  
  input[type="checkbox"]:checked + .slider:before {
    transform: translateX(20px);
  }
   